.header {
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: 1px solid black;
    background-color: lightpink;
    color: black;
    font-family: "poppins", sans-serif;
    height: 100px;
}

.nav-items>ul {
    list-style-type: none;
    display: flex;
    margin-top: 28px;
    margin-right: 640px;
}

.nav-items>ul>li {
    padding: 10px;
}

.logo {
    width: 100px;
    border-right: 1px solid black;
}

.card {
    width: 190px;
    border: 1px solid black;
    margin: 10px;
    font-family: "poppins", sans-serif;
    background-color: white;
    text-align: center;
    border-radius: 3%;
}

.img-div>img {
    margin-top: 13px;
    width: 175px;
    height: 140px;

}
.imgCard{
    width: 250px;
    height: 140px;

}

.divider {
    background-image: linear-gradient(90deg, #bebfc5 68%, transparent 0);
    background-size: 4px 1px;
    height: 1px;
    margin: 7px 4 17px;
}

.restaurantNameDesign {
    font-weight: bold;
}

.cusines {
    padding: 10px;
    font-size: 13px;
}

.rating {
    padding: 1px;
    font-size: 13px;
    margin-bottom: 17px;
}

.restaurant-List {
    display: flex;
    flex-wrap: wrap;
}

.search-input {
    margin-left: 10px;
    margin-right: 5px;
}

.shimmer-card{
    width: 200px;
    height:200px;
    background:lightgrey;
    margin: 20px;
}

.menu{
    display:flex
}